body {
    max-width: 1920px;
    margin: 0 auto;
    position: relative;
}

.wrapper {
    min-height: 100vh;
    height: 100%;
}

.content {
    flex: 1 0 auto;
}

.margin {
    &--c {
        margin: 0 auto;
    }
}

.cursor {
    &--pointer {
        cursor: pointer;
    }
}
